.Page {
    width:100%;
    min-height:100vh;
    background-color: #282828;
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.Box {
    width:500px;
}

.Robot {
    display:block;
    margin-left:auto;
    margin-right:auto;
    width:100px;
    height:100px;
}

.HeadText {
    font-family: 'Pangolin', cursive;
    font-size:30px;
    color: #00ff41;
    text-align: center;
    margin-top:20px;
    width:260px;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin-left: auto; /* Gives that scrolling effect as the typing happens */
    margin-right:auto;
    animation: 
        typing 4s steps(40, end),
        blink-caret .75s step-end infinite;
}

.HeadTextWrong {
    font-family: 'Pangolin', cursive;
    font-size:30px;
    color: #ff0000;
    text-align: center;
    margin-top:20px;
    width:300px;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin-left: auto; /* Gives that scrolling effect as the typing happens */
    margin-right:auto;
    animation: 
        typing2 4s steps(40, end),
        blink-caret .75s step-end infinite;
}

.HeadTextPass {
    font-family: 'Pangolin', cursive;
    font-size:30px;
    color: #00ff41;
    text-align: center;
    margin-top:20px;
    width:200px;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin-left: auto; /* Gives that scrolling effect as the typing happens */
    margin-right:auto;
    animation: 
        typing3 3s steps(40, end),
        blink-caret .75s step-end infinite;
}

.textinput {
    display:block;
    margin-left:auto;
    margin-right: auto;
    margin-top: 20px;
    width:300px;
    height:30px;
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:16px;
    background-color:#000000;
    color:#ffffff;
    padding-left:10px;
    padding-right:10px;
    border:2px solid #333333;
}

.submit {
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:18px;
    color: #ffffff;
    display:block;
    margin-left:auto;
    margin-right:auto;
    background-color: transparent;
    border:none;
    cursor:pointer;
    height:40px;
    width:100px;
    margin-top:20px;
}
.submit:hover {
    background-color: #444444;
}

