/* The typing effect */
@keyframes typing {
    from { width: 0 }
    to { width: 260px }
}

@keyframes typing2 {
    from { width: 0 }
    to { width: 300px }
}

@keyframes typing3 {
    from { width: 0 }
    to { width: 200px }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
}