.Page {
    width:100%;
    min-height:100vh;
    background-color: #282828;
    color: #ffffff;
    padding-left:20px;
    padding-top:20px;
}

.Beautifulmap {
    width:500px;
    height:500px;
    background-color:#ffffff;
    cursor: pointer;
    position:relative;
}

.currentPos {
    position: absolute;
    width: 40px;
    height: 40px;
    z-index:100;
}

.columnContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.logout {
    width:80px;
    height:40px;
    color: #ff0000;
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:25px;
    cursor:pointer;
    margin-left:20px;
    text-decoration: underline;
}

.namebox {
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:25px;
    color: #cccccc;
    display:flex;
    flex-direction: row;
    justify-content: center;
}

.name {
    color: #ffffff;
    margin-left:10px;
}

.debug {
    margin-right: 20px;
    margin-top:20px;
}

.debug2 {
    margin-right: 20px;
    margin-top:20px;
    width:400px;
}

.toggleButton {
    width:60px;
    height:30px;
    background-color:#3399ff;
    border-radius: 20px;
    padding-top:3px;
    padding-left:3px;
    padding-right: 3px;
    box-sizing: border-box;
    cursor:pointer;
    display:flex;
    justify-content: flex-start;
}

.toggleButton.right {
    justify-content: flex-end;
}

.toggleDot {
    width:24px;
    height:24px;
    border-radius: 25px;
    background-color: #ffffff;
}

.togglePane {
    width:280px;
    height:30px;
    line-height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif;
    font-weight:400;
    font-size:18px;
    margin-top:20px;
    margin-bottom:20px;
}

.secHead {
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:25px;
    text-decoration: underline;
}

.currentClick {
    width:10px;
    height:10px;
    border-radius: 10px;
    background-color: #0000FF;
    position:absolute;
    z-index:10;
}

.targetClick {
    width:10px;
    height:10px;
    border-radius: 10px;
    background-color: #00FF00;
    position:absolute;
    z-index:200;
}

.targetPath {
    width:10px;
    height:10px;
    border-radius: 10px;
    background-color: #FF00FF;
    position:absolute;
    z-index:90;
}

.statusText {
    text-decoration: underline;
    margin-right:10px;
}

.greenText {
    color:#00FF00;
}

.yellowText {
    color:#FFFF00;
}

.greyText {
    color:#aaaaaa;
}

.blueText {
    color:#0000FF;
}

.statusBox {
    margin-top:20px;
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:18px;
}

.high {
    font-family: 'Roboto', sans-serif;
    font-weight:700;
    font-size:18px;
    text-decoration: underline;
    margin-top:10px;
    margin-bottom:5px;
}